<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <CheckVote :vote="vote"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../表决列表/components/TopButtons'
    import CheckVote from './components/Check'
    import {getAttendVote} from '@/api/vote'

    export default {
        components: {
            TopButtons, CheckVote
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        data() {
            return {
                vote: {}
            }
        },
        methods: {
            fetch() {
                getAttendVote(this.thisId)
                    .then(res => {
                        this.vote = res.data;
                    })
                    .catch(res=>{
                        history.go(-1)
                    })
            }
        },
        created() {
            this.fetch();
        }
    }
</script>
