<template>
    <div>
        <p style="color: #af1a17;font-weight: bold;">
            表决操作
        </p>
        <div>
            <a-radio-group v-model="result">
                <a-radio :value="1">同意</a-radio>
                <a-radio :value="2">不同意</a-radio>
                <a-radio :value="3">弃权</a-radio>
            </a-radio-group>
        </div>
        <div style="height: 10px"></div>
        <a-button type="primary" @click="handleSubmit">提交</a-button>
    </div>
</template>

<script>
    import {attendSubmit} from '@/api/vote';

    export default {
        name: "Options",
        props: {
            id: {required: true, type: Number},
        },
        data() {
            return {
                result: 0,
            }
        },
        methods: {
            handleSubmit() {
                let result = this.result;
                if (!result) {
                    this.$message.warning('请进行选择');
                    return false;
                }

                attendSubmit(this.id, result)
                    .then(res => {
                        this.$message.success('投票成功');

                        console.log(res);
                    })
            }
        },
    }
</script>
