<template>
    <div class="columns">
        <div class="xwsh_nr">
            <div class="pad-30">
                <h1>{{vote.title}}</h1>
                <h2>{{vote.started_at_format}}</h2>

                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <div v-html="initiate_content"></div>
                    </a-col>
                    <a-col :span="22">
                        <downloadFiles v-if="files.length" :list="files"/>
                    </a-col>
                </a-row>

                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <options :id="thisId"/>
                    </a-col>
                </a-row>
            </div>
        </div>
    </div>
</template>

<script>
    import downloadFiles from '@/components/download/Files';
    import Options from './Options';

    export default {
        components: {downloadFiles, Options},
        props: {
            vote: {type: Object}
        },
        computed: {
            files() {
                if (!this.vote.hasOwnProperty('more')) {
                    return [];
                }
                if (!this.vote.more.hasOwnProperty('files')) {
                    return [];
                }
                return Array.isArray(this.vote.more.files) ? this.vote.more.files : [];
            },
            thisId() {
                if (!this.vote.hasOwnProperty('id')) {
                    return 0;
                }
                return this.vote.id;
            },
            initiate_content() {
                if (!this.vote.hasOwnProperty('initiate_content')) {
                    return null;
                }
                return this.vote.initiate_content;
            },
            publicity_content() {
                if (!this.vote.hasOwnProperty('publicity_content')) {
                    return null;
                }
                return this.vote.publicity_content;
            },
        },
        methods: {},
        mounted() {
        }
    }
</script>
